import React from 'react';
import * as sui from 'semantic-ui-react';
import Header from '../components/header';
import CSlider from "../components/controls/slider";
import CButton from '../components/controls/button';
import CText from '../components/controls/text';

class HomePage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }

    render() {
        return (
            <div>
                <Header>Home</Header>

                <p>Balken Helligkeit</p>
                <CSlider var="balken_brightness" />

                <p>Balken Schalter</p>
                <CSlider var="balken_switch" max={1} step={1}
                    labelConv={v => v >= 0.5 ? "On" : "Off"} />

                <p>Klotz</p>
                <CSlider var="klotz_switch" max={1} step={1} />
                <CSlider var="klotz_hue" max={360} />
                <CSlider var="klotz_saturation" />
                <CSlider var="klotz_brightness" />

                <p>Röhre (Verbraucht <CText var="m_stecki_sensor" />W)</p>
                <CSlider var="m_stecki_switch" min={0} max={1} step={1}
                    labelConv={v => v >= 0.5 ? "On" : "Off"} />

                <p>Rollladen</p>
                <sui.Button.Group secondary>
                    <CButton forceSend var="m_rolli" value={100} icon="chevron up" />
                    <CButton forceSend var="m_rolli" value="STOP" icon="stop circle" />
                    <CButton forceSend var="m_rolli" value={0} icon="chevron down" />
                </sui.Button.Group>

                <p>Lucas Stecki (Verbraucht <CText var="l_stecki_sensor" />W)</p>
                <CSlider var="l_stecki_switch" min={0} max={1} step={1}
                    labelConv={v => v >= 0.5 ? "On" : "Off"} />

                <p>Lucas Rolli</p>
                <sui.Button.Group secondary>
                    <CButton forceSend var="l_rolli" value={100} icon="chevron up" />
                    <CButton forceSend var="l_rolli" value="STOP" icon="stop circle" />
                    <CButton forceSend var="l_rolli" value={0} icon="chevron down" />
                </sui.Button.Group>
            </div>
        );
    }
}

export default HomePage;
